import axios from 'axios';
import React from 'react';
import { connect } from 'react-redux';
import { setDataAC } from '../redux/newsReducer';
import News from './News';

class NewsContainer extends React.Component {
  constructor(props) {
    super()
  }

  componentDidMount = () => {
    axios.get('https://newsapi.org/v2/top-headlines?sources=google-news-ru&apiKey=bb82a5810e234f13b51b63d3bc4d52ba')
/*     axios.get('https://cors-anywhere.herokuapp.com/http://api.mediastack.com/v1/', {
      headers: {
        "accsess_key" : "0d241d7597b786266cba6b2c77902383"
      }
    }) */
      .then(response => { this.props.setNewData(response.data.articles.slice(0, 1)) })
  }

  render() {

    return <>
    ver 2
      <News dataNews={this.props.dataNews} setNewData={this.props.setNewData} />
    </>
  }
}


let mapStateToProps = (state) => {
  return {
    dataNews: state.newsPage.dataNews,
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    setNewData: (dataNews) => {
      dispatch(setDataAC(dataNews))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewsContainer)
